import * as types from '../actions/types'

// const sites = localStorage.getItem('sites')
//   ? JSON.parse(localStorage.getItem('sites'))
//   : []

// const initialState = { sites }
const initialState = []

const storeSite = (state = initialState, action) => {
  switch (action.type) {
    case types.GET_SITES:
      // localStorage.setItem('sites', JSON.stringify(action.payload))
      return {
        sites: action.payload,
      }

    case types.REMOVE_ALL:
      // localStorage.setItem('sites', [])
      return { ...state, sites: [] }

    default:
      return state
  }
}

export default storeSite
